<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showadddialogVisible"
            ></el-button
          >
        </el-col>
      </el-row>
      <tree-table
        :data="catalist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text=""
        border
        :show-row-hover="false"
      >
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            type="success"
            size="mini"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showsetdialog(scope.row.cat_id)"
            ></el-button
          >
          <el-button type="danger" icon="el-icon-delete" size="mini"
          @click="deletecate(scope.row.cat_id)"
            ></el-button
          >
        </template>
      </tree-table>
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[3, 5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="adddialogVisible"
      width="50%"
      @close="closeadddialogVisible"
    >
      <el-form
        :model="addcateform"
        :rules="addcateformRules"
        ref="addcateformRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addcateform.cat_name"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-cascader
            expand-trigger="hover"
            v-model="selectkeys"
            :options="parentcatelist"
            :props="cateprops"
            @change="catechange"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false"> </el-button>
        <el-button type="primary" @click="addcate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setdialogVisible"
      width="50%"
      @close="closesetdialogVisible"
    >
      <el-form
        :model="setcateform"
        :rules="setcateformRules"
        ref="setcateformRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="setcateform.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setdialogVisible = false"> </el-button>
        <el-button type="primary" @click="setcate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      setdialogVisible:false,
      adddialogVisible:false,
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      catalist: [],
      total: 0,
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        { label: "", type: "template",template:'isok'},
        {label:'', type:'template',template:'order'},
        {label:'', type:'template',template:'opt'},
      ],
      addcateform:{
        cat_name:'',
        cat_pid:0,
        cat_level:0
      },
      addcateformRules:{
        cat_name:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      setcateformRules:{
        cat_name:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      parentcatelist:[],
      cateprops:{
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      setcateform:{},
      selectkeys:[],
    };
  },
  created() {
    this.getcatelist();
  },
  methods: {
    async getcatelist() {
      const { data: res } = await this.$http.get("categories", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.catalist = res.data.result;
      this.total = res.data.total;
    },
    handleSizeChange(newSize){
      this.queryInfo.pagesize = newSize
      this.getcatelist()
    },
    handleCurrentChange(newPage){
      this.queryInfo.pagenum = newPage
      this.getcatelist()
    },
    showadddialogVisible(){
      this.getparentcatelist()
      this.adddialogVisible = true
    },
    async getparentcatelist(){
      const { data : res} = await this.$http.get('categories',{params:{type:2}})
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.parentcatelist = res.data
    },
    catechange(){
      if(this.selectkeys.length > 0){
        this.addcateform.cat_pid = this.selectkeys[this.selectkeys.length - 1]
        this.addcateform.cat_level = this.selectkeys.length
        return
      }else{
        this.addcateform.cat_pid = 0
        this.addcateform.cat_level = 0
      }
    },
    closeadddialogVisible(){
      this.$refs.addcateformRef.resetFields()
      this.selectkeys = []
      this.addcateform.cat_pid = 0
      this.addcateform.cat_level = 0
    },
    closesetdialogVisible(){
      this.$refs.setcateformRef.resetFields()
    },
    addcate(){
      this.$refs.addcateformRef.validate(async valid =>{
        if(!valid) return
        const { data : res } = await this.$http.post('categories',this.addcateform)
        if (res.meta.status !== 201) {
        return this.$message.error("");
      }
        this.$message.success('')
        this.getcatelist()
        this.adddialogVisible = false
      })
    },
    async showsetdialog(id){
      const {data : res} = await this.$http.get('categories/' + id)
      if(res.meta.status !== 200){
        return this.$message.error('')
      }
      this.setcateform = res.data
      this.setdialogVisible = true
    },
    setcate(){
      this.$refs.setcateformRef.validate(async valid =>{
        if(!valid) return
          const {data : res} = await this.$http.put('categories/' + this.setcateform.cat_id,{
            cat_name:this.setcateform.cat_name  
        })
        if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.setdialogVisible = false
        this.getcatelist()
        this.$message.success('')
      })
    },
    async deletecate(id){
      const confirmresult = await this.$confirm (', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if(confirmresult !== 'confirm'){
          return this.$message.info('')
        }
        const { data : res } = await this.$http.delete('categories/' + id)
        if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getcatelist()
    },
  },
};
</script>

<style lang="less" scoped>
.el-row {
  margin-bottom: 15px;
}
.el-cascader {
  width: 100%;
}
</style>