<template>
  <div class="wel">
    <h1></h1>
    <h1>{{ newTime }}</h1>
    <el-calendar v-model="value"> </el-calendar>
    
  </div>
</template>

<script>
const days = ["", "", "", "", "", "", ""]; // 
var icnow = new Date(); // 
var interval; // 
export default {
  name: "app",
  data() {
    return {
      year: icnow.getFullYear(),
      month: icnow.getMonth() + 1,
      date: icnow.getDate(),
      day: days[icnow.getDay() - 1],
      time: icnow.toTimeString().substring(0, 8),
      value: new Date(),
    };
  },
  created() {
    interval = setInterval(() => {
      let icnow = new Date();
      this.year = icnow.getFullYear();
      this.month = icnow.getMonth() + 1;
      this.date = icnow.getDate();
      this.day = days[icnow.getDay()];
      this.time = icnow.toTimeString().substring(0, 8);
    }, 1000);
  },
  computed: {
    // 
    newTime: function () {
      return (
        this.year +
        "" +
        this.month +
        "" +
        this.date +
        " " +
        this.day +
        " " +
        this.time
      );
    },
  },
  beforeDestroy() {
    clearInterval(interval);
  },
};
</script>

<style lang="less" scoped>
.wel {
  text-align: center;
  font-size: larger;
}

</style>