<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addrolesdialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="rolelist" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row class="firstrow" :class="['bdbottom',i1===0 ? '' : 'bdtop']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
              <el-col :span="5">
                <el-tag  closable @close="removetag(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
            </el-col>
              <el-col :span="19">
                <el-row class="firstrow" v-for="(item2) in item1.children" :key="item2.id" >
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removetag(scope.row,item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" v-for="(item3) in item2.children" :key="item3.id" closable @close="removetag(scope.row,item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <!-- slot-scope="scope" -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="changerolesdialog(scope.row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="delroles(scope.row.id)"></el-button>
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showsetdialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
      <!--  -->
      <el-dialog
      title=""
      :visible.sync="setdialogVisible"
      width="50%"
      @close="closesetdialogVisible"
      >
      <el-tree
        :data="rightslist"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
        >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setdialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
        title=""
        :visible.sync="addrolesdialogVisible"
        width="50%"
        @close="rolesdialogclose"
        >
        <el-form :model="addroles" :rules="addRolesrules" ref="addRolesRef" label-width="70px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addroles.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="addroles.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button @click="addrolesdialogVisible = false"> </el-button>
        <el-button type="primary" @click="addrole"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="changerolesdialogVisible"
        width="50%"
        @close="changedialogclose"
        >
        <el-form :model="changeRoles" :rules="changeRolesrules" ref="changeRolesRef" label-width="70px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="changeRoles.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="changeRoles.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button @click="changerolesdialogVisible = false"> </el-button>
        <el-button type="primary" @click="changerole"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
export default {
  data(){
    return{
      roleId:'',
      addroles:{
        roleName:'',
        roleDesc:'',
      },
      changeRoles:{
        
      },
      rolelist:[],
      setdialogVisible:false,
      addrolesdialogVisible:false,
      changerolesdialogVisible:false,
      rightslist:[],
      treeProps:{
        label:'authName',
        children:'children',      
      },
      defKeys:[],
      addRolesrules:{
        roleName:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        roleDesc:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
      },
      changeRolesrules:{
        roleName:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        roleDesc:[
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
      },
    }
  },
  created(){
    this.getrolrlist()
  },
  methods:{
    async getrolrlist(){
      const { data : res } = await this.$http.get('roles')
      if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.rolelist = res.data
    },
    async removetag(role,rightId){
      const confirmresult =  await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if(confirmresult !== 'confirm'){
          return this.$message.info('')
        }
        const { data : res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        if(res.meta.status !== 200){
          return this.$message.error('')
        }
       role.children = res.data
        this.$message({
          message: '',
          type: 'success'
        })
    }, 
    async showsetdialog(role){
      this.roleId = role.id
      const { data : res} = await this.$http.get('rights/tree') 
      if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.rightslist = res.data
        this.getKeys(role,this.defKeys)
        this.setdialogVisible = true
    },
    getKeys(node,arr){
      if(!node.children){
        return arr.push(node.id)
      }
      node.children.forEach(item => 
        this.getKeys(item,arr)
      );
    },  
    closesetdialogVisible(){
      this.defKeys = []
    },
     rolesdialogclose(){
        this.$refs.addRolesRef.resetFields()
    },
    changedialogclose(){
      this.$refs.changeRolesRef.resetFields()
    },
    addrole(){
      this.$refs.addRolesRef.validate(async valid =>{
        if(!valid) return
        const{ data : res } = await this.$http.post('roles',this.addroles)
        if(res.meta.status !== 201){
          return this.$message.error('')
        }
        this.$message.success('')
        this.addrolesdialogVisible = false
        this.getrolrlist()
      })
    },
    async changerolesdialog(id){
      const {data : res} = await this.$http.get('roles/' + id)
      if(res.meta.status !== 200){
        return this.$message.error('')
      }
      this.changeRoles = res.data
      this.changerolesdialogVisible = true
    },
    changerole(){
      this.$refs.changeRolesRef.validate(async valid =>{
        if(!valid) return
          const {data : res} = await this.$http.put('roles/' + this.changeRoles.roleId,{
          roleName:this.changeRoles.roleName , roleDesc:this.changeRoles.roleDesc 
        })
        if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.changerolesdialogVisible = false
        this.getrolrlist()
        this.$message.success('')
      })
    },
    async delroles(id){
      const confirmresult = await this.$confirm (', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if(confirmresult !== 'confirm'){
          return this.$message.info('')
        }
        const { data : res } = await this.$http.delete('roles/' + id)
        if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getrolrlist()
    },
    async allotRights(){
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data : res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
      if(res.meta.status !== 200){
          return this.$message.error('')
        }
        this.$message.success('')
        this.getrolrlist()
        this.setdialogVisible = false
    },
  },
}
</script>

<style lang="less" scoped>
  .el-tag{
    margin: 7px;
  }
  .bdtop{
    border-top: 1px solid rgb(206, 206, 206);
  }
  .bdbottom{
    border-bottom: 1px solid rgb(206, 206, 206);
  }
  .firstrow{
    display: flex;
    align-items: center;
  }
</style>